/* wp color picker */
.wp-picker-container {
    .button {
        position: relative;
        height: 40px;
        border: 0 none;
        width: 140px;
        transition: 0.3s ease;
        border-radius: 3px;

        &:hover {
            background: rgba(0, 0, 0, 0.7);
        }

        .wp-color-result-text {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            height: 100%;
            width: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 1;
            font-size: 14px;
            background: #f7f7f7;
            color: #222;
        }
    }

    .wp-picker-input-wrap {
        label {
            width: 90px;

            input {
                height: 40px;
                padding: 0;
                text-align: center;
            }
        }
    }

    .hidden {
        display: none;
    }
}

.wp-picker-open+.wp-picker-input-wrap {
    display: block;
}

.wp-picker-container .wp-picker-input-wrap {
    padding: 15px 0;

    .screen-reader-text {
        display: none;
    }

    label {
        width: 90px;
        margin: 0;

        &+.button {
            margin-left: 10px;
            padding-top: 0;
            padding-bottom: 0;
            font-size: 15px;
        }

        input {
            width: 200px;
        }
    }
}


.diectorist-show {
    display: block !important;
}

.diectorist-hide {
    display: none !important;
}

.atbd_announcement_wrapper {
    .directorist_not-found {
        p {
            margin-bottom: 0;
        }
    }
}